import React, { Component } from 'react';
import { Grid } from 'react-mdl';
import ProjectCard from './ProjectCard';

export default class WebProject extends Component {
    state = {
        projects: [
            {
                id: 1,
                title: "Caramel's Food App",
                imageUrl: "data:image/jpeg;base64,",
                technologies: "React Native, React Bootstrap, HTML/CSS3, MySQL, AWS, Express.js",
                description: "Developed a web application with React, which calls a RESTful web API to fetch data about foods, in order to calculate and save the amount of nutrients a dog intakes every day. Integrating a user login portal with user authentication, to allow multiple users to use the application."
            },
            {
                id: 2,
                title: "HRMS/POS Gym Web Application",
                imageUrl: require('./assets/kabs.jpg'),
                technologies: "DJango, Python, Bootstrap, HTML/CSS3, MySQL, AWS",
                description: "Adopted SCRUM Software Development Life Cycle methodology to produce a Human Resource Management System (HRMS) and Point of Sales (POS) solution for KABS Tumbling & Fitness Gym. Developed a portal to manage monthly gym membership payments, fitness classes, trainer's schedules and calculate trainer's monthly pay."
            },
            {
                id: 3,
                title: "Earthquake API",
                imageUrl: "data:image/jpeg;base64,",
                technologies: "Python, JSON",
                description: ""
            },
            {
                id: 5,
                title: "Arithmatic Expression",
                imageUrl: require('./assets/pl4.jpg'),
                technologies: "HTML/CSS, php",
                description: ""
            }
        ]
    }
    render() {
        return (
            <Grid>
                {this.state.projects.map(project => (<ProjectCard key={project.id} title={project.title}
                    imageUrl={project.imageUrl} description={project.description} technologies={project.technologies} />))}
            </Grid>
        );
    }
}